<template>
	<view>
		<!-- 顶部区域 -->
		<view class="topBarCard">
			<!-- 搜索框 -->
			<view class="searchBox">
				<view class="searchButton"  @click="goSearchPage">
					搜索
				</view>
			</view>
			<!--信息图标  -->
			<view class="messageBox" @click="goMessagePage">

			</view>
		</view>
		<!-- 轮播图 -->
		<view class="slideCard">
			<swiper calss="swiperCards" :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000">
				<swiper-item>
					<view class="swiper-item"></view>
				</swiper-item>
				<swiper-item>
					<view class="swiper-item"></view>
				</swiper-item>
				<swiper-item>
					<view class="swiper-item"></view>
				</swiper-item>
			</swiper>

		</view>
		<!-- 主要内容展示卡片区域 -->
		<view class="mainContent">
			<view class="contentCard" @click="goInfoPage"></view>
			<view class="contentCard" @click="goInfoPage"></view>
			<view class="contentCard" @click="goInfoPage"></view>
		</view>
		<!-- //底部半透明框 -->
		<view class="bottomBar">
			<!-- //发布按钮 -->
			<view class="createButton" @click="goCreatePage">  
				
			</view>
			
		</view>
	</view>
</template>

<script>
	//import contentCard from '../../../components/chenCard.vue'
	export default {
		data() {
			return {

			}
		},
		methods: {
			goSearchPage(){
				uni.navigateTo({
					url:'../searchPage/searchPage'
				})
			},
			goMessagePage(){
				uni.navigateTo({
					url:'../messagePage/messagePage'
				})
			},
			goCreatePage(){
				uni.navigateTo({
					url:'../createPage/createPage'
				})
			},
			goInfoPage(){
				uni.navigateTo({
					url:'../infoPage/infoPage'
				})
			}
		},
		// components:{
		// 	"contentCard": contentCard,
		// },
	}
</script>

<style>
	//设置顶部框
	.topBarCard{
		width: 100%;
		height: 68px;
		display: flex;
		align-content: space-between;
		padding-top: 12px;
	}
	//搜索框样式
	.topBarCard>.searchBox{
		width: 250px;
		height: 34px;
		background: #FFFFFF;
		border-radius: 15px;
		margin-left: 10px;
	}
	//搜索图标
	.topBarCard>.searchCard>.searchButton{
		
	}
	//信息图标样式
	.topBarCard>.messageBox{
		width: 87px;
		height: 34px;
		background: #7D7D7D;
		border-radius: 34px;
		margin-left: 20px;
	}
	//包裹滑块的外边框样式
	.slideCard{
		width: 90%;
		margin: 0 auto;
		background-color: #515151;
		border-radius: 10px;
		margin-bottom: 10px;
	}
	//设置滑动图高度
	.slideCard>swiper{
		height: 227px;
	}
	//显示"最新推送"的提示文字
	.mainContent::before{
		content: '最新推送';
		display: block;
		width: 44px;
		height: 10px;
		font-size: 10px;
		font-family: Source Han Sans SC;
		font-weight: bold;
		color: #FFFFFF;
		line-height: 10px;
		opacity: 0.8;

	}
	//暂时!!之后会用组件替代,先看一下效果
	.mainContent>.contentCard{
		height: 200px;
		width: 90%;
		margin: 0 auto;
		background-color: #515151;
		margin-bottom: 10px;
		border-radius: 10px;
		margin-top: 10px;
	}
	//底部区域样式
	.bottomBar{
		position: fixed;
		bottom: 0px;
		background-color:#515151;
		width: 100%;
		height: 70px;
		opacity: 0.5;
		border-top-left-radius: 20px;
		border-top-right-radius: 20px;
	}
	//发布按钮样式
	.bottomBar>.createButton{
		position: relative;
		width: 60px;
		height: 60px;
		border-radius: 50%;
		background-color: #FFFFFF;
		border: 25px #515151 solid;
		left: 137px;
		bottom: 25px;
	}
</style>
